@use "settings";
@use "mixins";

html, body {
  overflow-x: hidden;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  outline: none !important;
}

body, p, h1, h2, h3, h4, h5, h6 {
  font-family: settings.$main-font;
  margin: 0;
  padding: 0;
}

input, button {
  outline: none;
}
